.App {
  text-align: center;
  padding: 0;
  margin: 0;
background-image: url(../img/_DSC8125.jpg);
background-position: center;
background-origin: inherit;
background-size: cover;
background-repeat: no-repeat;
  height: 100vh;
  display: flex;
  flex-direction: column;
  z-index: 1000;
  }
  #before{
    display: flex;
    content: "";
     border-left: 10vw solid transparent;
    border-right: 10vw solid transparent;
    border-bottom: 10vh solid rgba(240, 240, 240, 0.45);

    clip-path: polygon(0% 0%, 0% 100%, 50% 85%, 35% 0, 65% 0, 50% 85%, 0% 100%, 0 100%, 100% 100%, 100% 0%);    
    /* -webkit-box-shadow: -1px -27px 234px 162px rgba(0,0,0,0.75);
    -moz-box-shadow: -1px -27px 234px 162px rgba(0,0,0,0.75);
    box-shadow: -1px -27px 234px 162px rgba(0,0,0,0.75); */
  }
  #main{
    box-sizing: border-box;
    height: 75%;
    /* background-color: rgba(255, 99, 71, 0.45); */
       background-color: rgba(240, 240, 240, 0.45); 
    box-shadow: 0 1vh 1vh #4b4a4a;
    display: flex;
    justify-content: left;
    align-items: center;
}
