 .false{
    /* box-shadow: 0 0 5px 1px red; */
   color: rgb(255, 208, 0);
   text-shadow: 2px 2px 4px rgb(212, 45, 45);

}
.true{
    box-shadow: none;
}

.InputComp{
    padding: 10px;
    font-size: 18px;

}
.noInputComp{
    display: none;
}
.InputAnswer{
    display: flex;
    flex-direction: row;
    padding-left: 25px;
    padding-top:10px;
    margin-bottom: 12px;
    cursor: pointer;
    font-size: 18px;
   height: 50px;
   width: 180px;
}
.InputAnswer:hover{
  background-color: rgb(72, 82, 102);
}
.InputAnswer input {
    box-shadow: 5px 5px 15px -2px rgb(31, 35, 44);
    background: rgb(187, 200, 226);
    height: 20px;
}
.InputAnswer input[type='range']{
    -webkit-appearance: none;  /* Override default CSS styles */
  appearance: none;

  outline: none; /* Remove outline */
  opacity: 0.7; /* Set transparency (for mouse-over effects on hover) */
  -webkit-transition: .2s; /* 0.2 seconds transition on hover */
  transition: opacity .2s;
}
.InputAnswer input[type='range']::-webkit-slider-thumb {
    -webkit-appearance: none; 
    appearance: none;
    width: 25px; 
    height: 25px; 
    border-radius: 3px;
    background: rgb(43, 114, 245); 
    cursor: pointer; 
  }
  

  .InputAnswer input[type=radio],[type=checkbox]{
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  display: inline-block;
  position: relative;
  color: #666;
  top: 10px;
  width: 20px;
  border: 0;
  border-radius: 50px;
  cursor: pointer;     
  margin-right: 7px;
  outline: none;
  box-shadow: 5px 5px 15px -2px rgb(31, 35, 44);
  }
  .InputAnswer input[type=radio]:checked::before,[type=checkbox]:checked::before
{
     position: absolute;
     font: 15px 'Open Sans', sans-serif;
     left: 6px;
     top: 1px;
     font-weight: bold;
     content: '\02143';
     transform: rotate(40deg);
     color: rgb(240, 240, 240);
}

.InputAnswer input[type=radio]:checked,[type=checkbox]:checked
{
     background-color: rgb(43, 114, 245);
}